@import url('https://fonts.googleapis.com/css2?family=Rubik:wght@400;600&display=swap');


  


/* Typography */
body, ul, li, p, a, label, input, div {
    font-family: 'Rubik', sans-serif;
    font-size: 18px ;
    font-weight: 300 ;
  }
  .h1 {
    font-family: 'Rubik', sans-serif;
    font-size: 48px ;
    font-weight: 200 ;
  }
  .h2 {
    font-family: 'Rubik', sans-serif;
    font-size: 30px ;
    font-weight: 300;
  }
  .h3 {
    font-family: 'Rubik', sans-serif;
    font-size: 22px ;
  }
 
  /* General */
  .logo { font-weight: 500 ;}
  .text-warning {  color: #ede861 ;}
  .text-muted { color: #bcbcbc ;}
  .text-success { color: #E41A44 ;}
  .text-light { color: #cfd6e1 ;}
  .text-normal { color: #1d242d ;}
  .bg-dark { background-color: #212934 ;}
  .bg-light { background-color: #e9eef5 ;}
  .bg-black { background-color: #1d242d ;}
  .bg-claro { background-color: #5E2E3F ;}



  .bg-success { background-color: #E41A44 ;}
  .btn-success {
    background-color: #E41A44 ;
    border-color: #56ae6c ;
  }
  .pagination .page-link:hover {color: #000;}
  .pagination .page-link:hover, .pagination .page-link.active {
    background-color: #F67375;
    color: #fff;
  }

  .card {
    align-content: center;
    border: 1px solid grey;
    border-radius: 6px;
    margin-bottom: 10px;
    margin-right: 10px;
    overflow: hidden;
  }

  .card p {
    padding: 5px;
    text-align: center;
  }

  .card img {
    max-height: 170px;
  }

  .card-grid {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }

  /* Nav */
  #template_nav_top { min-height: 40px;}
  #template_nav_top * { font-size: .9em ;}
  #template_main_nav a { color: #212934;}
  #template_main_nav a:hover { color: #F67375;}
  #template_main_nav .navbar .nav-icon { margin-right: 20px;}

  /* Hero Carousel */
 
  /* Accordion */
  .template-accordion a { color: #000;}
  .template-accordion a:hover { color: #333d4a;}
  /* Shop */
  .shop-top-menu a:hover { color: #F67375 ;}
  /* Product */
  .product-wap { box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.10);}
  .product-wap .product-color-dot.color-dot-red { background:#f71515;}
  .product-wap .product-color-dot.color-dot-blue { background:#6db4fe;}
  .product-wap .product-color-dot.color-dot-black { background:#000000;}
  .product-wap .product-color-dot.color-dot-light { background:#e0e0e0;}
  .product-wap .product-color-dot.color-dot-green { background:#0bff7e;}
  .card.product-wap .card .product-overlay {
    background: rgba(0,0,0,.2);
    visibility: hidden;
    opacity: 0;
    transition: .3s;
  }
  .card.product-wap:hover .card .product-overlay {
    visibility: visible;
    opacity: 1;
  }
  .card.product-wap a { color: #000;}
  #carousel-related-product .slick-slide:focus { outline: none ;}
  #carousel-related-product .slick-dots li button:before {
    font-size: 15px;
    margin-top: 20px;
  }
  /* Brand */
  .brand-img {
    filter: grayscale(100%);
    opacity: 0.5;
    transition: .5s;
  }
  .brand-img:hover {
    filter: grayscale(0%);
    opacity: 1;
  }
  /* Carousel Hero */
  #template-mo-zay-hero-carousel .carousel-indicators li {
    margin-top: -50px;
    background-color: #E41A44;
  }
  #template-mo-zay-hero-carousel .carousel-control-next i,
  #template-mo-zay-hero-carousel .carousel-control-prev i {
    color: #E41A44 ;
    font-size: 2.8em ;
  }
  /* Carousel Brand */
  .tempaltemo-carousel .h1 {
    font-size: .5em ;
    color: #000 ;
  }
  /* Services */
  .services-icon-wap {transition: .3s;}
  .services-icon-wap:hover, .services-icon-wap:hover i {color: #fff;}
  .services-icon-wap:hover {background: #8a1e1a;}
  /* Contact map */
  .leaflet-control a, .leaflet-control { font-size: 10px ;}
  .form-control { border: 1px solid #e8e8e8;}
  /* Footer */
  #tempaltemo_footer a { color: #dcdde1;}
  #tempaltemo_footer a:hover { color: #F67375;}
  #tempaltemo_footer ul.footer-link-list li { padding-top: 10px;}
  #tempaltemo_footer ul.footer-icons li {
    width: 2.6em;
    height: 2.6em;
    line-height: 2.6em;
  }
  #tempaltemo_footer ul.footer-icons li:hover {
    background-color: #0f5cd8;
    transition: .5s;
  }
  #tempaltemo_footer ul.footer-icons li:hover i {
    color: #fff;
    transition: .5s;
  }
  #tempaltemo_footer .border-light { border-color: #2d343f ;}
  /*
  // Extra small devices (portrait phones, less than 576px)
  // No media query since this is the default in Bootstrap
  */
  /* Small devices (landscape phones, 576px and up)*/
  .product-wap .h3, .product-wap li, .product-wap i, .product-wap p {
    font-size: 12px ;
  }
  .product-wap .product-color-dot {
    width: 6px;
    height: 6px;
  }

  @media (min-width: 576px) {
    .tempaltemo-carousel .h1 { font-size: 1em ;}
  }

  /*// Medium devices (tablets, 768px and up)*/
  @media (min-width: 768px) {
    #template_main_nav .navbar-nav {max-width: 450px;}
   }

  /* Large devices (desktops, 992px and up)*/
  @media (min-width: 992px) {
    #template_main_nav .navbar-nav {max-width: 550px;}
    #template-mo-zay-hero-carousel .carousel-item {min-height: 30rem ;}
    .product-wap .h3, .product-wap li, .product-wap i, .product-wap p {font-size: 18px ;}
    .product-wap .product-color-dot {
      width: 12px;
      height: 12px;
    }
  }

  /* Extra large devices (large desktops, 1200px and up)*/
  @media (min-width: 1200px) {}



  .logos-img{
    width: 100px;
  }

  .icon-facebook i{
    height: 20px;
    width: 20px;
    text-align: center;
    padding: 5px;
    margin: 0px 5px;
    color: #555;
    font-size: 18px;
  }
  .icon-facebook:hover i{
    background-color: white;
    border-radius: 50%;

  }
  .enlaces:hover i{
    color: #F67375;
  }

  /* botones */
  .botones {
    display: inline-block;
    background-color: #E41A44;
    color: #FFF;
    padding: .75rem 1rem;
    border-radius: .5rem;
    transition: .3s;
    text-decoration: none;
  }

  .leaf-container{
  width: 100%;
  height: 100vh;
  }
/*======================================
    Product CSS
========================================*/
/* Single Product*/
.single-product {
    border: 1px solid #eee;
    border-radius: 4px;
    -webkit-transition: all 0.4s ease;
    transition: all 0.4s ease;
    margin-top: 30px;
    -webkit-box-shadow: 0px 0px 20px #00000012;
    box-shadow: 0px 0px 20px #00000012;
    padding: 8px;
    background: #fff;
  }

  .single-product .product-image {
    overflow: hidden;
    position: relative;
  }

  .single-product .product-image .sale-tag {
    background: #f73232;
    border-radius: 2px;
    font-size: 12px;
    color: #fff;
    font-weight: bold;
    position: absolute;
    top: 0;
    padding: 5px 10px;
    left: 0;
    z-index: 22;
  }

  .single-product .product-image .new-tag {
    background: #0167F3;
    border-radius: 2px;
    font-size: 12px;
    color: #fff;
    font-weight: bold;
    position: absolute;
    top: 0;
    padding: 5px 10px;
    left: 0;
    z-index: 22;
  }

  .single-product .product-image .button {
    position: absolute;
    left: 50%;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
    bottom: -60px;
    -webkit-transition: all 0.4s ease;
    transition: all 0.4s ease;
    opacity: 0;
    visibility: hidden;
  }

  .single-product .product-image .button .btn {
    padding: 12px 20px;
    font-size: 13px;
    font-weight: 600;
    width: 140px;
  }

  .single-product .product-image .button .btn i {
    font-size: 18px;
      position: relative;
      top: 2px;
  }

  .single-product .product-image img {
    width: 100%;
    -webkit-transition: all 0.4s ease;
    transition: all 0.4s ease;
  }

  .single-product:hover .product-image .button {
    bottom: 30px;
    opacity: 1;
    visibility: visible;
  }

  .single-product:hover .product-image img {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
  }

  .single-product .product-info {
    padding: 20px;
    background-color: #fff;
  }

  .single-product .product-info .category {
    color: #888;
    font-size: 13px;
    display: block;
    margin-bottom: 2px;
  }

  .single-product .product-info .title a {
    font-size: 16px;
    font-weight: 700;
    color: #081828;
  }

  @media only screen and (min-width: 768px) and (max-width: 991px),
  (max-width: 767px) {
    .single-product .product-info .title a {
      font-size: 15px;
    }
  }

  .single-product .product-info .title a:hover {
    color: #0167F3;
  }

  .single-product .product-info .review {
    margin-top: 5px;
  }

  .single-product .product-info .review li {
    display: inline-block;
  }

  .single-product .product-info .review li i {
    color: #fecb00;
    font-size: 13px;
  }

  .single-product .product-info .review li span {
    display: inline-block;
    margin-left: 4px;
    color: #888;
    font-size: 13px;
  }

  .single-product .product-info .price {
    margin-top: 15px;
  }

  .single-product .product-info .price span {
    font-size: 17px;
    font-weight: 700;
    color: #0167F3;
    display: inline-block;
  }

  .single-product .product-info .price .discount-price {
    margin: 0;
    color: #aaaaaa;
    text-decoration: line-through;
    font-weight: normal;
    margin-left: 10px;
    font-size: 14px;
    display: inline-block;
  }

  /* End Single Product */
  .trending-product {
    background-color: #f9f9f9;
  }

  .trending-product .section-title {
    margin-bottom: 20px;
  }
